/*
========================================
core/units
----------------------------------------
The master palettes of
- colors
- whitespace
- typescale
----------------------------------------
This file is not meant to be modified
by USWDS project teams. Change your
project values in
project/uswds-project-settings
----------------------------------------
*/

/*
----------------------------------------
Spacing grid multiplier
----------------------------------------
*/

$uswds-spacing-grid-base: 8px;

/*
----------------------------------------
Spacing grid
----------------------------------------
*/

$neg-prefix: 'neg';
$uswds-spacing:(
  smaller: (
    '1px':                      1px,
    '2px':                      2px,
  ),
  small: (
    '05':                     grid-units(0.5),  // 4px
    1:                        grid-units(1),   // 8px
    '105':                    grid-units(1.5), // 12px
    2:                        grid-units(2),   // 16px
    '205':                    grid-units(2.5), // 20px
    3:                        grid-units(3),   // 24px
  ),
  smaller-negative: (
    '#{$neg-prefix}-1px':     -1px,
    '#{$neg-prefix}-2px':     -2px,
  ),
  small-negative: (
    '#{$neg-prefix}-05':     grid-units(-0.5),  // -4px
    '#{$neg-prefix}-1':      grid-units(-1),   // -8px
    '#{$neg-prefix}-105':    grid-units(-1.5), // -12px
    '#{$neg-prefix}-2':      grid-units(-2),   // -16px
    '#{$neg-prefix}-205':    grid-units(-2.5), // -20px
    '#{$neg-prefix}-3':      grid-units(-3),   // -24px
  ),
  medium: (
    4:                        grid-units(4),   // 32px
    5:                        grid-units(5),   // 40px
    6:                        grid-units(6),   // 48px
    7:                        grid-units(7),   // 56px
    8:                        grid-units(8),   // 64px
    9:                        grid-units(9),   // 72px
    10:                       grid-units(10),  // 80px
    15:                       grid-units(15),  // 120px
  ),
  medium-negative: (
    '#{$neg-prefix}-4':       grid-units(-4),  // -32px
    '#{$neg-prefix}-5':       grid-units(-5),  // -40px
    '#{$neg-prefix}-6':       grid-units(-6),  // -48px
    '#{$neg-prefix}-7':       grid-units(-7),  // -56px
    '#{$neg-prefix}-8':       grid-units(-8),  // -64px
    '#{$neg-prefix}-9':       grid-units(-9),  // -72px
    '#{$neg-prefix}-10':      grid-units(-10), // -80px
    '#{$neg-prefix}-15':      grid-units(-15), // -120px
  ),
  large: (
    'card':                   grid-units(20),  // 160px
    'card-lg':                grid-units(30),  // 240px
    'mobile':                 grid-units(40),  // 320px
  ),
  larger: (
    'mobile-lg':              grid-units(60),  // 480px
    'tablet':                 grid-units(80),  // 640px
    'tablet-lg':              grid-units(110), // 880px
  ),
  largest: (
    'desktop':                grid-units(128), // 1024px
    'desktop-lg':             grid-units(150), // 1200px
    'widescreen':             grid-units(175), // 1400px
  ),
  special: (
    0:      0,
    'auto':   auto,
  ),
);

$uswds-column-gaps: (
  '2px':      2px,
  .5:       '05',
  1:        1,
  3:        3,
  4:        4,
  6:        6,
);

/*
----------------------------------------
Breakpoints
----------------------------------------
*/

$uswds-breakpoints: map-collect(
  map-get($uswds-spacing, large),
  map-get($uswds-spacing, larger),
  map-get($uswds-spacing, largest)
);

/*
----------------------------------------
Units
----------------------------------------
*/

$uswds-spacing-em: (
 small: (
   '05em':  .5em,
   1em:        1em,
   105em:  1.5em,
   2em:        2em,
 ),
);

/*
----------------------------------------
Layout grid widths
----------------------------------------
*/

$uswds-layout-grid-widths: (
  1:   100% * (1 / 12),
  2:   100% * (2 / 12),
  3:   100% * (3 / 12),
  4:   100% * (4 / 12),
  5:   100% * (5 / 12),
  6:   100% * (6 / 12),
  7:   100% * (7 / 12),
  8:   100% * (8 / 12),
  9:   100% * (9 / 12),
  10:  100% * (10 / 12),
  11:  100% * (11 / 12),
  12:  100% * (12 / 12),
);

/*
----------------------------------------
Type scale
----------------------------------------
*/

$all-font-definitions: map-merge(
  $uswds-font-definitions,
  $theme-font-definitions
);

$uswds-type-scale: (
  'micro':   10px,
  1:   12px,
  2:   13px,
  3:   14px,
  4:   15px,
  5:   16px,
  6:   17px,
  7:   18px,
  8:   20px,
  9:   22px,
  10:  24px,
  11:  28px,
  12:  32px,
  13:  36px,
  14:  40px,
  15:  48px,
  16:  56px,
  17:  64px,
  18:  80px,
  19:  120px,
  20:  140px,
);

/*
----------------------------------------
Measure
----------------------------------------
*/

$uswds-measure-smaller:	    40ch;
$uswds-measure-small:	      60ch;
$uswds-measure-base:	      66ch;
$uswds-measure-large:	      72ch;
$uswds-measure-larger:	    77ch;

/*
----------------------------------------
Line height
----------------------------------------
*/

$uswds-line-height: (
  1:    1,
  2:    1.15,
  3:    1.35,
  4:    1.5,
  5:    1.62,
  6:    1.75,
);

/*
----------------------------------------
Base cap height
----------------------------------------
500px 'N' height measured in Sketch.
Used for normalizing font sizes.
Current normalized to Apple system
fonts.
----------------------------------------
*/

$uswds-base-cap-height: 362px;

